@import '../../stylesheets/variables';
.btn{
    color: $white;
    background-color: $primary-color;
    font-size: 12px;
    text-transform: uppercase;
    border: none;
    padding: 15px 25px;
    border-radius: 5px;
    cursor: pointer;
    &:focus{
        outline: none;
    }
    &.secondary{
        background-color: $secondary-color;
    }
}