@import '../../stylesheets/variables';

.container{
    background-color: $grey;
    padding: 120px 50px;
    display: flex;
    align-items: center;
    .image{
       flex: 1;
    }
    .content{
        flex: 1;
        margin-left: 50px;
        .title{
            font-size: 38px;
            line-height: 48px;
            margin-bottom: 20px;
        }
        .text{
            font-size: 18px;
            line-height: 26px;
            margin-bottom: 40px;
        }
    }
}