@import '../../stylesheets/variables';

.container{
    padding: 0 50px;
    display: flex;
    align-items: center;
    .image{
       flex: 1.25;
       .imgTag{
           vertical-align: middle;
           &.pushup{
               margin-top: -20px;
           }
       }
    }
    .content{
        flex: 1;
        margin-left: 50px;
        .title{
            font-size: 32px;
            line-height: 40px;
            margin-bottom: 10px;
        }
        .text{
            font-size: 24px;
            line-height: 28px;
        }
        &.right{
            margin-left: 0;
            margin-right: 50px;
        }
    }
}